import React from 'react';
import './search.scss';
import documents from './images/Document.svg';
import folders from './images/Folders.svg';
import searchImage from './images/searchImage.svg';
import SearchForm from './search-form/SearchForm';


function Search() {
    return (
        <div className='container'>
            <section className='section1'>
                <div className='contentLeft'>
                    <h3 className='h h3'>     .</h3>
                    <p className='subTitle'>  . <br />  ,   </p>
                </div>
                <div className='contentRight'>
                    <img src={documents} alt=""
                        className='document' width={91} />
                    <img src={folders} alt=""
                        className='folders' width={140} />
                </div>
            </section>
            <section className='section2'>
                <SearchForm />
                <div className='contentRight'>
                    <img src={searchImage} alt="" width={442} className='imgRight' />
                </div>
            </section>
        </div>
    )
}

export default Search;