import React from 'react';
import './button.scss';


const tooltipLogin = '     ';
const tooltipSearch = '   ';


function Button({ type, name, value, disabled }) {
    return (
        <button
            type={type}
            disabled={disabled}
            className={`button button${name}`}
            {...(disabled ? { datatooltip: tooltipLogin } : {})}
            {...(disabled ? { datatooltip: tooltipSearch } : {})}
        >
            {value}
        </button>
    )
}

export default Button;